* {
	font-family: Tahoma,Helvetica,Arial,sans-serif;
	-webkit-touch-callout:none;
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
::selection {
    background: #5ba4a4;
    color: #fff;
    text-shadow: none;
}
h1, h2, h3, h4, h5, h6 {
	font-family: Tahoma,Helvetica,Arial,sans-serif;
}
a {
	color: #262626;
	text-decoration: none;
    -webkit-transition: color 0.25s;
	-moz-transition: color 0.25s;
	-ms-transition: color 0.25s;
	-o-transition: color 0.25s;
	transition: color 0.25s;
}
a:hover,
a:focus {
	color: #4db2ec;
	text-decoration: none;
}
.clearfix {
	clear: both !important;
}
.clearfix::after {
    clear: both;
}
.clearfix::before {
    content: " ";
    display: table;
}
.row {
	max-width: 62.500rem;
}
html {
    background: transparent;
    height: 100%;
    overflow-x: hidden;
    font-size: 100%;
    line-height: 1.3em;
    color: #262626;
}
body {
    background: #2A1300; //url('/img/purty_wood.png') repeat  
}
.container {
	background: #fff;
}
.main {
	padding-left: 0.9375rem;
	padding-right: 0.9375rem;
}
header {
	padding-left: 0.9375rem;
	padding-right: 0.9375rem;
}
footer {
	background: #333;
	padding: 0.9375rem;
	font-size: 0.813rem;
	p {
		margin: 0;
		color: #fff;
	}
	a {
		color: #fff;
	}
	a:hover {
		color: #C50000;
	}
}
a.logo {
    padding: 0.313rem 0.938rem;
	> img {
		height: 4rem;
		max-width: 100%;
	}
}
//go to top button
a.gotop {
	color: #4db2ec;
	position: fixed;
	bottom: -2.500rem;
	right: 1.875rem;
	opacity: 0.75;
	z-index: 9999;
	-webkit-transition: all 0.5s;
	-moz-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
	i.fa {
		font-size: 2.500rem;
	}
}
a.gototop {
	bottom: 2.500rem;
}
// end go to top button

.search-form {
	.button {
		background-color: #C50000;
	}
}

.box {
	margin: 1.563rem 0;
}
.box-title {
	h3 {
		padding: 0.313rem 0;
		font-size: 1.563rem;
		border-bottom: 3px solid #eee;
		margin-bottom: 0.938rem;
		text-transform: uppercase;
	}
}
.box-seemore {
	text-align: center;
	a {
		font-size: 1.063rem;
		text-transform: uppercase;
		padding: 0.375rem 0.313rem;
		background: #e2e2e2;
		color: #333;
		display: block;
	}
	a:hover {
		color: #fff;
		background: #C50000;
	}
}

.box-grid {
	.callout {
		margin-bottom: 1.875rem;
		padding: 0;
		border: none;
		border-radius: 4px;
		box-shadow: 0px 0px 10px 0px rgba(39,49,65,0.1);
		.grid-image {
			background: #fff;
			a {
				display: block;
				img {
					width: 100%;
				}
			}
		}
		.grid-title {
			padding: 0.625rem 0;
			h2 {
				font-size: 1rem;
				margin: 0;
				font-weight: bold;
				text-align: center;
				a {
					
				}
			}
		}
		.grid-price {
			p {
				color: #C50000;
				margin-bottom: 0.313rem;
				text-align: center;
				strong {
					font-size: 1.125rem;
				}
				span {
					font-style: line-through;
				}
			}
		}
	}
}

//menu
.topmenu {
	display:table;
	ul.menu {
		margin:1.563rem 0;
		padding:0;
		list-style:none;
		display: table;
		ul {
			margin: 0;
			z-index: 9999;
			opacity:0;
			position:absolute;
			top:160%;
			visibility:hidden;
			transition:all .4s ease;
			-webkit-transition:all .4s ease;
			ul {
				top:0%;
				left:160%;
			}
			li:hover > ul {
				top:0%;
				left:100%;
				opacity:1;
				visibility:visible;
			}
			li {
				background-color:#2A1300;
				float:none;
				a {
					text-align: left;
					color: #fff;
					min-width: 12.500rem;
				}
			}
		}
		li:hover > ul {
			opacity:1;
			top:100%;
			visibility:visible;
		}
		li {
			list-style: none;
			float:left;
			position:relative;
			cursor: pointer;
			a {
				text-align: center;
				text-decoration:none;
				display:block;
				padding:0.625rem 0.938rem;
				min-width:6.250rem;
				text-shadow: 0px -1px 0px rgba(0,0,0,.2);
			}
		}
		li:hover {
			background-color:#462103;
			a {
				color: #fff;
			}
		}
		li.current {
			a {
				color: #C50000;
			}
			a:hover {
				color: #fff;
			}
		}
		> li {
			> a {
				text-transform: uppercase;
				padding:0.938rem;
			}
		}
		span.dropBottom,span.dropRight {
			display: block;
			box-shadow:inset 2px 0px 0px #C50000;
			position: absolute;
			left: 0px;
			width:100%;
			height: 100%;
			top: 0px;
		}
		span.dropBottom {
			box-shadow:inset 0px 2px 0px #C50000;
			position: absolute;
			width: 100%;
			bottom: 0px;
		}
	}
}
